.page-wrapper {
	// display: flex;
	// justify-content: space-between;
	// gap: 10px;
	overflow-x: hidden;

	@media screen and (min-width: 1440px) {
		display: flex;
		justify-content: space-between;
		gap: 30px;
		max-width: 1440px;
		margin-inline: auto;
	}
}

.main {
	width: calc(100vw - 70px);
	padding-top: 20px;
	padding-left: 10px;

	@media screen and (min-width: 1440px) {
		padding: 0;
	}
}
