a {
  color: currentColor;
  text-decoration: none;
}

address {
  font-style: inherit;
}

body {
  font-family: "Inter", sans-serif;
  font-size: 15px;
  font-weight: 400;
  line-height: 1.6;
  color: #767676;
  background-color: #ffffff;
  transition: background-color 250ms;
}
@media screen and (min-width: 1440px) {
  body {
    background-color: #f0f0f6;
  }
}

button {
  border: none;
  border-radius: 0;
  cursor: pointer;
}

html {
  scroll-behavior: smooth;
}

img {
  display: block;
  max-width: 100%;
  height: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-block: 0;
  color: #2b2b2b;
}

p {
  margin-block: 0;
}

ul.reset {
  margin-block: 0;
  padding-left: 0;
  list-style: none;
  line-height: 0;
}

.edu-n-work__title {
  margin-bottom: 25px;
  font-size: 32px;
  line-height: 1.25;
  text-align: center;
  text-transform: capitalize;
}
.edu-n-work__wrapper {
  padding-block: 20px;
}
.edu-n-work__block::after {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  margin-block: 20px;
  background-color: #767676;
  opacity: 0.3;
}
.edu-n-work__item-period {
  display: inline-block;
  padding-block: 4px;
  padding-inline: 7px;
  font-size: 12px;
  line-height: 1;
  color: #2b2b2b;
  background-color: #ffb400;
}
@media screen and (min-width: 768px) {
  .edu-n-work, .hero {
    max-width: 970px;
    margin-bottom: 70px;
  }
  .edu-n-work__subtitle--work, .edu-n-work__subtitle {
    margin-bottom: 50px;
    padding-inline: 15%;
    text-align: center;
  }
  .edu-n-work__wrapper {
    width: 100%;
    padding-inline: 30px;
    background-color: #ffffff;
  }
  .edu-n-work__block::after {
    margin-block: 30px;
  }
  .edu-n-work__item {
    display: flex;
    column-gap: 30px;
  }
  .edu-n-work__item-data {
    min-width: 250px;
  }
  .edu-n-work__item-title {
    margin-bottom: 20px;
    font-size: 18px;
    font-weight: 500;
    line-height: 1.2222222222;
  }
  .edu-n-work__item-link {
    transition: color 250ms;
  }
  .edu-n-work__item-link:hover, .edu-n-work__item-link:focus {
    color: #ffb400;
  }
  .edu-n-work__item-subtitle {
    color: #2b2b2b;
  }
  .edu-n-work__item-description {
    width: 100%;
  }
}
@media screen and (min-width: 1440px) {
  .edu-n-work__wrapper {
    margin-bottom: 70px;
  }
  .edu-n-work__item {
    display: flex;
    column-gap: 50px;
  }
  .edu-n-work__item-data {
    min-width: 300px;
  }
}

.skills__title, .social__title {
  margin-bottom: 10px;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.2222222222;
}
@media screen and (min-width: 1440px) {
  .skills__title, .social__title {
    margin-bottom: 15px;
  }
}
.skills::after, .brief::after, .social::after {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  margin-top: 25px;
  background-color: #767676;
  opacity: 0.3;
}

.page-wrapper {
  overflow-x: hidden;
}
@media screen and (min-width: 1440px) {
  .page-wrapper {
    display: flex;
    justify-content: space-between;
    gap: 30px;
    max-width: 1440px;
    margin-inline: auto;
  }
}

.main {
  width: calc(100vw - 70px);
  padding-top: 20px;
  padding-left: 10px;
}
@media screen and (min-width: 1440px) {
  .main {
    padding: 0;
  }
}

.general-info {
  display: none;
}
@media screen and (min-width: 1440px) {
  .general-info {
    display: block;
    max-width: 305px;
    padding: 50px 40px 25px;
    background-color: #ffffff;
    transition: background-color 250ms;
  }
}

.social {
  margin-bottom: 25px;
}
@media screen and (min-width: 1440px) {
  .social {
    width: 225px;
  }
  .social__photo {
    margin-bottom: 31px;
  }
}
@media screen and (min-width: 1440px) {
  .social-mobile {
    display: none;
  }
}
.social__photo {
  margin-inline: auto;
  margin-bottom: 10px;
}
.social__title {
  text-align: center;
  transition: color 250ms;
}
.social__subtitle {
  margin-bottom: 15px;
  font-size: 15px;
  line-height: 1.6;
  text-align: center;
}
.social__list {
  display: flex;
  justify-content: space-between;
  width: 225px;
  margin-inline: auto;
}
@media screen and (min-width: 1440px) {
  .social__list {
    width: auto;
  }
}
.social__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
  color: #2b2b2b;
  border-radius: 50%;
  background-color: #ffb400;
  transition: background-color 250ms;
}
.social__link:hover, .social__link:focus {
  background-color: #f0f0f6;
}
.social__icon {
  fill: currentColor;
}
.brief {
  margin-bottom: 25px;
}
.brief__item {
  display: flex;
  justify-content: space-between;
  font-size: 15px;
  line-height: 1.6;
  color: #2b2b2b;
}
.brief__item:not(:last-child) {
  margin-bottom: 10px;
}
.brief__name {
  padding-inline: 5px;
  background-color: #ffb400;
}
.brief__value {
  transition: color 250ms;
}
.brief__value--accent {
  font-weight: 700;
  text-transform: capitalize;
  color: #7eb942;
}
.skills {
  margin-bottom: 25px;
}
.skills__title {
  transition: color 250ms;
}
.skills__list {
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
}
@media screen and (min-width: 1440px) {
  .skills__list {
    display: block;
  }
}
.skills__item {
  padding-inline: 5px;
  border-radius: 5px;
  background-color: #2b2b2b;
  color: #f0f0f6;
  font-size: 15px;
  line-height: 1.6;
}
@media screen and (min-width: 1440px) {
  .skills__item {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    padding-inline: 0;
    border-radius: 0;
    background-color: transparent;
    color: #767676;
    line-height: 1.6;
  }
  .skills__item:not(:last-child) {
    margin-bottom: 10px;
  }
}
.skills__item--lang {
  justify-content: space-between;
}
.skills__item--bullet::before {
  content: "";
  width: 15px;
  height: 15px;
  margin-right: 15px;
  background-image: url("../../assets/img/icon-bullet.svg");
}
.skills__item--bullet:not(:last-child), .skills__item--extra:not(:last-child) {
  margin-bottom: 5px;
}
.skills__name, .skills__value {
  transition: color 250ms;
}
.skills__name--lang, .skills__value {
  margin-bottom: 5px;
}
.skills__level {
  flex-grow: 1;
  width: 100%;
  height: 6px;
  padding: 1px;
  border: 1px solid #ffb400;
  border-radius: 3px;
  background-color: #ffffff;
  transition: background-color 250ms;
}
.skills__level::after {
  content: "";
  display: block;
  height: 2px;
  border-radius: 1px;
  background-color: #ffb400;
}
.skills__level--fluent::after {
  width: 100%;
}
.skills__level--b2::after {
  width: 57.1428571429%;
}
.skills__level--a2::after {
  width: 28.5714285714%;
}
.get-cv {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 225px;
  height: 40px;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.2142857143;
  color: #2b2b2b;
  text-transform: uppercase;
  border: none;
  border-radius: 5px;
  background-color: #ffb400;
  transition: color 250ms;
}
.get-cv:hover, .get-cv:focus {
  color: #ffffff;
}
.get-cv__icon {
  display: block;
  width: 16px;
  height: 16px;
  margin-left: 20px;
  fill: currentColor;
}

.hero {
  display: none;
}
@media screen and (min-width: 1440px) {
  .hero {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    gap: 69px;
    width: 100%;
    padding: 18px 49px 0 60px;
    background-image: url("../img/homepage/hero__bg.svg");
    background-color: #ffffff;
    background-repeat: no-repeat;
    background-size: cover;
    transition: background-color 250ms;
  }
  .hero__wrapper {
    padding-top: 25px;
  }
  .hero__title {
    margin-bottom: 10px;
    font-size: 48px;
    font-weight: 700;
    line-height: 1.2291666667;
    color: #767676;
    transition: color 250ms;
  }
  .hero__title--selection {
    color: #ffb400;
  }
  .hero__title--accent {
    color: #2b2b2b;
    transition: color 250ms;
  }
  .hero__subtitle {
    margin-bottom: 10px;
    font-size: 16px;
    line-height: 1.5;
  }
  .hero__subtitle:last-of-type {
    margin-bottom: 15px;
  }
  .hero__link {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 150px;
    height: 50px;
    margin-bottom: 18px;
    font-size: 16px;
    font-weight: 500;
    line-height: 1.1875;
    color: #2b2b2b;
    border-radius: 5px;
    background-color: #ffb400;
    transition: color 250ms;
  }
  .hero__link:hover, .hero__link:focus {
    color: #ffffff;
  }
  .hero__icon {
    width: 16px;
    height: 16px;
    margin-left: 9px;
    fill: currentColor;
  }
}

@media screen and (max-width: 1439px) {
  .edu-n-work:nth-of-type(2)::after {
    content: "";
    display: block;
    width: 100%;
    height: 1px;
    margin-block: 20px;
    background-color: #767676;
    opacity: 0.3;
  }
}
.edu-n-work--final {
  margin-bottom: 0;
}
.edu-n-work__title {
  transition: color 250ms;
}
.edu-n-work__subtitle--work {
  margin-bottom: 25px;
}
.edu-n-work__wrapper {
  transition: background-color 250ms;
}
.edu-n-work__wrapper--final {
  margin-bottom: 0;
}
.edu-n-work__item-title {
  transition: color 250ms;
}
.edu-n-work__item-link {
  transition: color 250ms;
}
.edu-n-work__item-subtitle {
  transition: color 250ms;
}
.edu-n-work__item-text:not(:last-child) {
  margin-bottom: 10px;
}
.edu-n-work__get-cv-btn {
  width: 225px;
  margin-inline: auto;
  margin-bottom: 50px;
}
.edu-n-work__list {
  margin-block: 0 10px;
  padding-left: 20px;
}
.edu-n-work__list-item--header {
  margin-left: -20px;
  font-weight: 700;
  list-style: none;
}

.wrapper-nav {
  position: fixed;
  top: 0;
  right: 0;
  width: 60px;
  height: 100vh;
  background-color: #767676;
  transition: background-color 250ms;
}
@media screen and (min-width: 1440px) {
  .wrapper-nav {
    position: relative;
    width: 108px;
    min-width: 108px;
    height: inherit;
    background-color: #ffffff;
  }
}

.main-nav {
  position: sticky;
  top: 0;
  right: 0;
  padding-top: 30px;
  padding-inline: 10px;
}
@media screen and (min-width: 1440px) {
  .main-nav {
    width: 108px;
    padding: 52px 34px;
  }
}
.main-nav__item {
  margin-bottom: 43px;
}
.main-nav__item:first-child {
  margin-bottom: 100px;
}
.main-nav__link {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  color: #767676;
  border-radius: 50%;
  background-color: #f0f0f6;
  transition: color 250ms, background-color 250ms;
}
.main-nav__link:hover, .main-nav__link:focus, .main-nav__link--current {
  color: #2b2b2b;
  background-color: #ffb400;
}
.main-nav__link:hover::before, .main-nav__link:focus::before, .main-nav__link--current::before {
  visibility: visible;
  opacity: 1;
}
.main-nav__link::before {
  content: "";
  position: absolute;
  bottom: 110%;
  left: -50%;
  display: block;
  width: 80px;
  padding: 5px;
  font-size: 10px;
  font-weight: 500;
  line-height: 1.25;
  text-align: center;
  color: #ffffff;
  border-radius: 2px;
  background-color: #2b2b2b;
  visibility: hidden;
  opacity: 0;
  transition: color 250ms, background-color 250ms, visibility 250ms, opacity 250ms;
}
.main-nav__link--dark-mode {
  transition: color 250ms, background-color 250ms;
}
.main-nav__link--dark-mode::before {
  content: "Dark Mode";
}
.main-nav__link--home::before {
  content: "Home Page";
}
.main-nav__link--education::before {
  content: "Education";
}
.main-nav__link--employment::before {
  content: "Employment";
}
.main-nav__link--portfolio::before {
  content: "Portfolio";
}
.main-nav__link--testimonials::before {
  content: "Testimonials";
}
.main-nav__icon {
  display: block;
  width: 18px;
  height: 18px;
  fill: currentColor;
}
.main-nav__icon--dark-mode {
  width: 25px;
  height: 25px;
}